// Global Filter Component
const GlobalFilter = ({ filter, setFilter }: any) => {
  return (
    <span className="ms-auto">
      <input
        value={filter || ""}
        onChange={(e) => setFilter(e.target.value)}
        className="form-control !w-auto"
        placeholder="Search jobs..."
      />
    </span>
  );
};

const Jobs = () => {
  // Define columns
  const columns = useMemo(
    () => [
      {
        Header: 'Job Title',
        accessor: 'jobTitle',
      },
      {
        Header: 'Company',
        accessor: 'company',
      },
      {
        Header: 'Location',
        accessor: 'location',
      },
      {
        Header: 'Experience',
        accessor: 'experience',
      },
      {
        Header: 'Salary',
        accessor: 'salary',
      },
      {
        Header: 'Posted By',
        accessor: 'postedBy',
      },
      {
        Header: 'Actions',
        accessor: 'id',
        disableSortBy: true,
        Cell: ({ row }: any) => (
          <div className="flex items-center gap-2">
            <div className="hs-tooltip ti-main-tooltip">
              <button
                type="button"
                className="hs-tooltip-toggle ti-btn ti-btn-icon ti-btn-sm ti-btn-warning"
              >
                <i className="ri-eye-line"></i>
                <span
                  className="hs-tooltip-content ti-main-tooltip-content py-1 px-2 !bg-black !text-xs !font-medium !text-white shadow-sm"
                  role="tooltip"
                >
                  View
                </span>
              </button>
            </div>
            <div className="hs-tooltip ti-main-tooltip">
              <button
                type="button"
                className="hs-tooltip-toggle ti-btn ti-btn-icon ti-btn-sm ti-btn-info"
              >
                <i className="ri-pencil-line"></i>
                <span
                  className="hs-tooltip-content ti-main-tooltip-content py-1 px-2 !bg-black !text-xs !font-medium !text-white shadow-sm"
                  role="tooltip"
                >
                  Edit
                </span>
              </button>
            </div>
            <div className="hs-tooltip ti-main-tooltip">
              <button
                type="button"
                className="hs-tooltip-toggle ti-btn ti-btn-icon ti-btn-sm ti-btn-danger"
              >
                <i className="ri-delete-bin-line"></i>
                <span
                  className="hs-tooltip-content ti-main-tooltip-content py-1 px-2 !bg-black !text-xs !font-medium !text-white shadow-sm"
                  role="tooltip"
                >
                  Delete
                </span>
              </button>
            </div>
          </div>
        ),
      },
    ],
    []
  )

  const data = useMemo(() => JOBS_DATA, [])

  const tableInstance: any = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0, pageSize: 10 },
    },
    useGlobalFilter,
    useSortBy,
    usePagination
  )

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    state,
    setGlobalFilter,
    page,
    nextPage,
    previousPage,
    canNextPage,
    canPreviousPage,
    pageOptions,
    gotoPage,
    pageCount,
    setPageSize,
  } = tableInstance

  const { globalFilter, pageIndex, pageSize } = state

  return (
    <Fragment>
      <Seo title={"Jobs"} />
      <Pageheader currentpage="Jobs" activepage="ATS" mainpage="Jobs" />
      <div className="grid grid-cols-12 gap-6">
        <div className="xl:col-span-12 col-span-12">
          <div className="box custom-box">
            <div className="box-header flex items-center justify-between flex-wrap gap-4">
              <div className="box-title">
                Jobs
                <span className="badge bg-light text-default rounded-full ms-1 text-[0.75rem] align-middle">
                  {JOBS_DATA.length}
                </span>
              </div>
              <div className="flex flex-wrap gap-2">
                <Link
                  href="#!"
                  scroll={false}
                  className="hs-dropdown-toggle ti-btn ti-btn-primary-full !py-1 !px-2 !text-[0.75rem] me-2"
                  data-hs-overlay="#create-job-modal"
                >
                  <i className="ri-add-line font-semibold align-middle"></i>Create Job
                </Link>
                <button
                  type="button"
                  className="ti-btn ti-btn-success !py-1 !px-2 !text-[0.75rem] me-2"
                >
                  <i className="ri-upload-2-line font-semibold align-middle me-1"></i>Import
                </button>
                <button
                  type="button"
                  className="ti-btn ti-btn-info !py-1 !px-2 !text-[0.75rem] me-2"
                >
                  <i className="ri-file-excel-2-line font-semibold align-middle me-1"></i>Export to Excel
                </button>
                <button
                  type="button"
                  className="ti-btn ti-btn-danger !py-1 !px-2 !text-[0.75rem]"
                >
                  <i className="ri-delete-bin-line font-semibold align-middle me-1"></i>Delete
                </button>
              </div>
            </div>
            <div className="box-body !p-0">
              <div className="mb-4 flex flex-wrap items-center gap-2 p-4 border-b dark:border-defaultborder/10">
                <select
                  className="selectpage border me-1"
                  value={pageSize}
                  onChange={(e) => setPageSize(Number(e.target.value))}
                >
                  {[10, 25, 50, 100].map((size) => (
                    <option key={size} value={size}>
                      Show {size}
                    </option>
                  ))}
                </select>
                <GlobalFilter filter={globalFilter} setFilter={setGlobalFilter} />
              </div>
              <div className="table-responsive">
                <table {...getTableProps()} className="table whitespace-nowrap min-w-full">
                  <thead>
                    {headerGroups.map((headerGroup: any) => (
                      <tr {...headerGroup.getHeaderGroupProps()} key={Math.random()}>
                        {headerGroup.headers.map((column: any) => (
                          <th
                            {...column.getHeaderProps(column.getSortByToggleProps())}
                            scope="col"
                            className="text-start"
                            key={Math.random()}
                          >
                            <div className="flex items-center gap-2">
                              <span className="tabletitle">{column.render('Header')}</span>
                              <span>
                                {column.isSorted ? (
                                  column.isSortedDesc ? (
                                    <i className="ri-arrow-down-s-line text-[0.875rem]"></i>
                                  ) : (
                                    <i className="ri-arrow-up-s-line text-[0.875rem]"></i>
                                  )
                                ) : (
                                  ''
                                )}
                              </span>
                            </div>
                          </th>
                        ))}
                      </tr>
                    ))}
                  </thead>
                  <tbody {...getTableBodyProps()}>
                    {page.map((row: any) => {
                      prepareRow(row)
                      return (
                        <tr {...row.getRowProps()} className="border border-defaultborder" key={Math.random()}>
                          {row.cells.map((cell: any) => {
                            return (
                              <td {...cell.getCellProps()} key={Math.random()}>
                                {cell.render('Cell')}
                              </td>
                            )
                          })}
                        </tr>
                      )
                    })}
                  </tbody>
                </table>
              </div>
            </div>
            <div className="box-footer !border-t-0">
              <div className="flex items-center flex-wrap gap-4">
                <div>
                  Showing {pageIndex * pageSize + 1} to {Math.min((pageIndex + 1) * pageSize, data.length)} of {data.length} entries{' '}
                  <i className="bi bi-arrow-right ms-2 font-semibold"></i>
                </div>
                <div className="ms-auto">
                  <nav aria-label="Page navigation">
                    <ul className="ti-pagination mb-0">
                      <li className={`page-item ${!canPreviousPage ? 'disabled' : ''}`}>
                        <button
                          className="page-link"
                          onClick={() => previousPage()}
                          disabled={!canPreviousPage}
                        >
                          Prev
                        </button>
                      </li>
                      {pageOptions.slice(Math.max(0, pageIndex - 1), Math.min(pageOptions.length, pageIndex + 3)).map((page: number) => (
                        <li
                          key={page}
                          className={`page-item ${pageIndex === page ? 'active' : ''}`}
                        >
                          <button
                            className="page-link"
                            onClick={() => gotoPage(page)}
                          >
                            {page + 1}
                          </button>
                        </li>
                      ))}
                      <li className={`page-item ${!canNextPage ? 'disabled' : ''}`}>
                        <button
                          className="page-link text-primary"
                          onClick={() => nextPage()}
                          disabled={!canNextPage}
                        >
                          Next
                        </button>
                      </li>
                    </ul>
                  </nav>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  )
}

export default Jobs
